                                                    </filter>
                                                </defs>
                                                
                                                <rect x="50" y="100" width="100" height="100" fill="pink" filter="url(#ff-at)" stroke="pink">
                                                    <animate
                                                        attributeName="y" 
                                                        from="100" 
                                                        to="0"
                                                        dur="5s" 
                                                        repeatCount="indefinite"
                                                    />
