                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p class=".body-1">1. <code>&lt;feComposite in="A" in2="B" operator="over" /&gt;</code>标签用来合并两个输入源A和B，有如下合成类型</p>
                                                            <p class=".body-1">2. <code>over</code>表示A在B之上，A和B都显示</p>
                                                            <p class=".body-1">3. <code>in</code>表示A的一部分在B的不透明区域之内，只显示A</p>
                                                            <p class=".body-1">4. <code>out</code>表示A的一部分在B的不透明区域之外，只显示A</p>
                                                            <p class=".body-1">5. <code>atop</code>表示A和B重叠区域，A和B都显示</p>
                                                            <p class=".body-1">6. <code>xor</code>表示A和B重叠区域之外的区域，A和B都显示</p>
                                                            <p class=".body-1">7. <code>lighter</code>表示A和B重叠区域显示更亮</p>
                                                            <p class=".body-1">8. <code>arithmetic</code>表示A和B重叠的效果由公式<code>k1*A*B + k2*A + k3*B + k4</code>计算得出</p>
                                                            <v-select
                                                                :items="compositeModes"
                                                                label="合成模式"
                                                                style="width: 150px;"
                                                                color="#7cb242"
                                                                v-model="compositevalue"
                                                            ></v-select>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{compositehtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>

                    <v-flex xs12>
