                                <v-card-text>
                                    <v-layout row wrap>
                                        <v-flex xs12 sm6 pr-1>
                                            <svg width="200" height="150" xmlns="http://www.w3.org/2000/svg">
                                                <defs>
                                                    <filter id="fcmx" filterUnits="userSpaceOnUse">
                                                        <feConvolveMatrix
                                                            preserveAlpha="true"
                                                            kernelMatrix="1 0 0
                                                                          0 0 0
                                                                          0 0 -1"
                                                            bias="0.5"/>
                                                    </filter>
                                                </defs>
                                                
                                                <image xlink:href="../../assets/submenu.jpeg" x="0" y="0"
                                                    height="150" width="200" style="filter:url(#fcmx);" />

                                            </svg>
                                            <svg width="200" height="150" xmlns="http://www.w3.org/2000/svg">
                                                <defs>
                                                    <filter id="fcmx1" filterUnits="userSpaceOnUse">
                                                        <feConvolveMatrix
                                                            preserveAlpha="true"
                                                            kernelMatrix="1 0 0
                                                                          0 1 0
                                                                          0 0 -1"
                                                            divisor="3"
                                                            bias="0.2"/>
                                                    </filter>
                                                </defs>
                                                
                                                <image xlink:href="../../assets/submenu.jpeg" x="0" y="0"
                                                    height="150" width="200" style="filter:url(#fcmx1);" />
                                            </svg>
                                            <svg width="200" height="150" xmlns="http://www.w3.org/2000/svg">
                                                <defs>
                                                    <filter id="fcmx2" filterUnits="userSpaceOnUse">
                                                        <feConvolveMatrix
                                                            preserveAlpha="true"
                                                            kernelMatrix="1 0 0
                                                                          0 1 0
                                                                          0 0 -1"
                                                            bias="0.2"/>
                                                    </filter>
                                                </defs>
                                                
                                                <image xlink:href="../../assets/submenu.jpeg" x="0" y="0"
                                                    height="150" width="200" style="filter:url(#fcmx2);" />
                                            </svg>
                                            <svg width="200" height="150" xmlns="http://www.w3.org/2000/svg">
                                                <defs>
                                                    <filter id="fcmx3" filterUnits="userSpaceOnUse">
                                                        <feConvolveMatrix
                                                            preserveAlpha="true"
                                                            kernelMatrix="2 0 -1
                                                                          0 1 0
                                                                          1 0 -1"
                                                            divisor="3"
                                                            bias="0.1"/>
                                                    </filter>
                                                </defs>
                                                
                                                <image xlink:href="../../assets/submenu.jpeg" x="0" y="0"
                                                    height="150" width="200" style="filter:url(#fcmx3);" />
                                            </svg>
                                        </v-flex>

                                        <v-flex xs12 sm6>
                                            <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                                <v-tab key="explain" ripple>解释</v-tab>
                                                <v-tab key="html" ripple>HTML</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p class=".body-1">1. <code>feConvolveMatrix</code>元素通过一种算法计算当前像素点和其相邻像素点的像素值运算后的结果作为新的像素点</p>
                                                            <p class=".body-1">2. 像素点和其临近点：假设<code>p</code>为当前像素点，则在默认情况下其周围一共有8个像素点假设为<code>a b c d e f g h</code>，排列方式如下</p>
<pre>
a b c
d p e
f g h
</pre>
                                                            <p class=".body-1">3. 算法：<code>kernelMatrix</code>接受9个参数分别代表像素点的参数值，其排列方式和上面一致，假设参数值为<code>av bv cv dv pv ev fv gv hv</code>，偏移值属性<code>bias</code>表示主要用来调整值的范围，另外还有一个属性<code>divisor</code>可以保证计算结果尽量在0~1之间，算法如下</p>
<pre>
[
    (a*av + b*bv + c*cv
    d*dv + p*pv + e*ev
    f*fv + g*gv + h*hv) / divisor
] + bias
</pre>
                                                            <p class=".body-1">4. <code>preserveAlpha="true"</code>可以让算法不计算<code>alpha</code>值</p>

                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                                <v-tab-item key="html">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <pre class="code">
                                                                {{convolvehtml}}
                                                            </pre>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
                                            </v-tabs>
                                        </v-flex>
                                    </v-layout>
                                </v-card-text>
                            </v-card>
                        </v-hover>
