                                                <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                                <v-tab-item key="explain">
                                                    <v-card flat>
                                                        <v-card-text>
                                                            <p class=".body-1">1. <code>feDisplacementMap</code>是采用一种算法计算像素点的位置，根据<code>in2</code>传入的图片源移动<code>in</code>图片源的像素</p>
                                                            <p class=".body-1">2. <code>feDisplacementMap</code>接受5个参数分别是<code>in</code>、<code>in2</code>、<code>scale</code>、<code>xChannelSelector</code>、<code>yChannelSelector</code>其中最后两个参数表示通道选择器可选参数有<code>R</code>、<code>G</code>、<code>B</code>、<code>A</code></p>
                                                            <p class=".body-1">3. 在介绍算法公式之前，必须告诉你的是：为了将计算单位全部统一成相对单位需要将滤镜设置<code>primitiveUnits="objectBoundingBox"</code>，也就是说公式中的像素位置采用的是<code>0~1</code>这种占比形式来确定的，颜色通道选择的颜色分量也是位于<code>0~1</code>范围内，你可以理解为<code>0~255</code>除了<code>255</code></p>
                                                            <p class=".body-1">4. 假设<code>in2</code>图片源像素的坐标为<code>(x, y)</code>，通道选择器选择的x,y的颜色分量为<code>(cx, cy)</code>则新像素点坐标为</p>
<pre>
    x' = x + scale*(cx - 0.5);
    y' = y + scale*(cy - 0.5);
</pre>
                                                            <p class=".body-1">5. 我们需要<code>in2</code>引入的图片源作为计算基准，这种图片源一般都是通过<code>feImage</code>标签的<code>xlink:href=""</code>引入外部资源，这个地方会有很严重的bug(很多浏览器不支持)，避免bug必须使用datauri的形式引入资源，详见demo</p>
                                                            <p class=".body-1">6. 目前我测试的谷歌和safari都是可以运行的</p>
                                                        </v-card-text>
                                                    </v-card>
                                                </v-tab-item>
